<template>
  <div>
    <Backbar title="生成订单"></Backbar>
    <div class="order_box">
      <div class="order_one">
        <div v-for=" (item) in  addressList" class="address_one">
          <div class="pannel-body">
            <div class="address-info">
              <div class="flex_align">
                <p class="b_operate">
                  <input type="radio" v-model="address_id" v-bind:value="item.id" name="address_id" class="address-radio">
                </p>
                <div class="b_address">
                  <p>
                    <span class="b_provence">{{ item.provence }}</span>
                    <span class="b_city">{{ item.city }}</span>
                    <span class="b_area">{{ item.area }}</span>
                  </p>
                  <p>
                    {{ item.detail_address }}
                  </p>
                  <p>
                    <span class="b_name">{{ item.name }}</span>
                    <span class="b_tel">{{ item.tel }}</span>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div v-for=" (goods) in  goods.goods_list" class="pannel-body">
          <div class="goods-img">
            <img v-bind:src="goods.goods_img" alt="">
          </div>
          <div class="goods-info">
            <header class="goods_info_t">
              <div class="goods_title flex_align">
                <p class="b_name">{{ goods.goods_name }}</p>
                <p class="b_amount">{{ goods.amount }}</p>
                <p class="b_price">￥{{ goods.goods_price }}</p>
              </div>
            </header>
          </div>
        </div>
      </div>
    </div>
    <button @click="pay_order">生成订单 ￥{{ goods.totalCost }}</button>
    <Fixednav></Fixednav>
  </div>
</template>

<script>
  import Backbar from './small_components/Back_bar';
  import Fixednav from './small_components/Fixed_nav';

  export default {
    name: 'generateOrder',
    data () {
      return {
        address_id: 0,
        goods: [],
        addressList: []
      };
    },
    mounted () {
      if (!this.$store.getters.getLogin) {
        this.$router.replace('/login');
      }
      this.$http.get(window.api.addressList).then(function (res) {
        var data = JSON.parse(res.bodyText);
        this.addressList = data;
      });
      this.$http.get(window.api.cart).then(function (res) {
        var data = JSON.parse(res.bodyText);
        this.goods = data;
      });
    },
    methods: {
      pay_order () {
        if (!this.$store.getters.getLogin) {
          this.$router.replace('/login');
        }
        if (!this.address_id) {
          alert('请选择收货地址');
        }
        this.$http.post(window.api.addOrder, {
          address_id: this.address_id
        }, {emulateJSON: true}).then(function (res) {
          var data = JSON.parse(res.bodyText);
          if (data.status === 'true') {
            alert(data.message);
            this.$router.replace('/orderDetail?id=' + data.order_id);
          } else {
            alert(data.message);
          }
        });
      }
    },
    components: {
      Backbar,
      Fixednav
    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
  .order_box {
    margin: 1rem 0 .6rem;
  }
  .address-radio{
    width: .5rem;
    height: .5rem;
  }
  button{
    margin: 0 auto;
    margin-bottom: 1.5rem;
    display:block;
    width: 9rem;
    height: 1rem;
    font-size:.5rem;
    background:#4cd964;
    border:none;
    outline:none;
    color:#fff;
    border-radius:.1rem;
  }
  .order_one {
    background: #fff;
    margin-bottom: .2rem;
    .pannel-head{
      padding: .3rem .4rem;
      font-weight: bold;
      font-size: .4rem;
      border-bottom: 1px solid #666;
    }
    .pannel-body{
      padding: .3rem .4rem;
      .goods-img{
        width: 1rem;
        height: 1rem;
        float: left;
        img {
          width: 100%;
        }
      }
      .goods-info{
        height:1rem;
        line-height:1rem;
        margin-left: 1.4rem;
        font-size:.4rem;
        .b_name{
          width:8rem;
        }
        .b_amount{
          width:2rem;
        }
        .b_price{
          width:2rem;
          text-align:right;
        }
      }
      .address-info{
        font-size:.4rem;
        .b_operate{
          width:1.5rem;
        }
        .b_address{
          width:8rem;
          text-align:left;
        }
      }
    }
  }
</style>



