  editAddress: '/static/editAddress.txt',
  // 获得订单列表接口
  orderList: '/static/orderList.txt',
  // 获得指定订单接口
  order: '/static/order.txt',
  // 获得商家列表接口
  businessList: '/static/businessList.txt',
  // 获得指定商家接口
  business: '/static/business.txt',
  // 保存购物车接口
  addCart: '/static/addCart.txt',
