<template>
  <div class="login_box">
    <Backbar title="注册"></Backbar>
    <div class="input_login">
      <input type="search" placeholder="用户名" v-model="username" maxlength="18">
      <input type="search" placeholder="手机号码" v-model="tel" maxlength="11">
      <div class="sms-box">
        <input type="search" maxlength="6" placeholder="请输入验证码" v-model="sms">
        <button type="submit" @click="get_sms($event)">获取验证码</button>
      </div>
      <input type="password" placeholder="密码" v-model="password" maxlength="18">
      <input type="password" placeholder="确认密码" v-model="conPassword" maxlength="18">
      <button @click="cheack_n_p">注册</button>
    </div>
  </div>
</template>

<script>
  import Backbar from './small_components/Back_bar';

  export default {
    name: 'regist',
    data () {
      return {
        username: '',
        tel: '',
        sms: '',
        password: '',
        conPassword: ''
      };
    },
    methods: {
      cheack_n_p () {
        if (this.username === '') {
          alert('用户名不能为空');
          return;
        }
        if (this.tel === '') {
          alert('手机号码不能为空');
          return;
        }
        if (this.sms === '') {
          alert('验证码不能为空');
          return;
        }
        if (this.password === '') {
          alert('密码不能为空');
          return;
        }
        if (this.conPassword === '') {
          alert('确认密码不能为空');
          return;
        }
        if (this.conPassword !== this.password) {
          alert('两次密码不一致');
          return;
        }
        this.$http.post(window.api.regist, {
          username: this.username,
          tel: this.tel,
          sms: this.sms,
          password: this.password
        }, {emulateJSON: true}).then(function (res) {
          var data = JSON.parse(res.bodyText);
          if (data.status === 'true') {
            alert(data.message);
            this.$router.replace('/login');
          } else {
            alert(data.message);
          }
        });
      },
      get_sms (event) {
        if (this.tel === '') {
          alert('手机号码不能为空');
          return;
        }
        var btn = event.currentTarget;
        btn.className = 'disactive';
        btn.disabled = true;
        var time = 10;
        btn.innerHTML = time;
        var timer = setInterval(function () {
          time--;
          btn.innerHTML = time;
          if (time <= 0) {
            clearInterval(timer);
            btn.innerHTML = '获取验证码';
            btn.className = '';
            btn.disabled = false;
          }
        }, 1000);
        this.$http.get(window.api.sms + '?tel=' + this.tel).then(function (res) {
          var data = JSON.parse(res.bodyText);
          if (data.status !== 'true') {
            alert(data.message);
          }
        });
      }
    },
    components: {
      'Backbar': Backbar
    }
  };
</script>

<style lang="less" scoped>
  .input_login {
    margin-top: 1.35rem;
    input {
      height: 1rem;
      width: 10rem;
      padding: .6rem;
      box-sizing: border-box;
      border: none;
      outline: none;
      font-size: .45rem;
      display: block;
    }
    button {
      margin: .6rem auto;
      display: block;
      width: 9rem;
      height: 1rem;
      font-size: .5rem;
      background: #4cd964;
      border: none;
      outline: none;
      color: #fff;
      border-radius: .1rem;
    }
    .sms-box {
      background-color: #fff;
      padding-right:.6rem;
      &:after {
        content: "";
        height: 0;
        display: block;
        clear: both;
      }
      input {
        float: left;
        width: 5.8rem;
      }
      button {
        float: right;
        width: 3.5rem;
        margin: 0;
      }
      button.disactive {
        background-color: #ccc;
      }
    }
  }

</style>



