<template>
  <div class="login_box">
    <Backbar title="修改收货地址"></Backbar>
    <div class="input_login">
      <input type="hidden" v-model="id" maxlength="11">
      <input type="search" placeholder="姓名" v-model="name" maxlength="11">
      <input type="search" placeholder="手机号码" v-model="tel" maxlength="11">
      <input type="select" placeholder="省" v-model="provence" maxlength="10">
      <input type="search" placeholder="市" v-model="city" maxlength="10">
      <input type="search" placeholder="区" v-model="area" maxlength="10">
      <input type="search" placeholder="详细地址" v-model="detail_address" maxlength="10">
      <button @click="save_address">保存</button>
    </div>
  </div>
</template>

<script>
  import Backbar from './small_components/Back_bar';

  export default {
    name: 'editAddress',
    data () {
      return {
        id: '',
        name: '',
        tel: '',
        provence: '',
        city: '',
        area: '',
        detail_address: ''
      };
    },
    methods: {
      save_address () {
        if (this.name === '') {
          alert('姓名不能为空');
          return;
        }
        if (this.tel === '') {
          alert('手机号码不能为空');
          return;
        }
        if (this.provence === '') {
          alert('省份不能为空');
          return;
        }
        if (this.city === '') {
          alert('城市不能为空');
          return;
        }
        if (this.area === '') {
          alert('区县不能为空');
          return;
        }
        if (this.detail_address === '') {
          alert('详细地址不能为空');
          return;
        }
        this.$http.post(window.api.editAddress, {
          id: this.id,
          name: this.name,
          tel: this.tel,
          provence: this.provence,
          city: this.city,
          area: this.area,
          detail_address: this.detail_address
        }, {emulateJSON: true}).then(function (res) {
          var data = JSON.parse(res.bodyText);
          if (data.status === 'true') {
            alert(data.message);
            this.$router.replace('/address');
          } else {
            alert(data.message);
          }
        });
      }
    },
    mounted () {
      if (!this.$store.getters.getLogin) {
        this.$router.replace('/login');
      }
      var parms = this.$route.query;
      var id = parms.id;
      this.id = id;
      this.$http.get(window.api.address + '?id=' + id).then(function (res) {
        var data = JSON.parse(res.bodyText);
        this.name = data.name;
        this.tel = data.tel;
        this.provence = data.provence;
        this.city = data.city;
        this.area = data.area;
        this.detail_address = data.detail_address;
      });
    },
    components: {
      'Backbar': Backbar
    }
  };
</script>

<style lang="less" scoped>
  .input_login {
    margin-top: 1.35rem;
    input {
      height: 1rem;
      width: 10rem;
      padding: .6rem;
      box-sizing: border-box;
      border: none;
      outline: none;
      font-size: .45rem;
      display: block;
    }
    button {
      margin: .6rem auto;
      display: block;
      width: 9rem;
      height: 1rem;
      font-size: .5rem;
      background: #4cd964;
      border: none;
      outline: none;
      color: #fff;
      border-radius: .1rem;
    }
    .sms-box {
      background-color: #fff;
      padding-right:.6rem;
      &:after {
        content: "";
        height: 0;
        display: block;
        clear: both;
      }
      input {
        float: left;
        width: 5.8rem;
      }
      button {
        float: right;
        width: 3.5rem;
        margin: 0;
      }
      button.disactive {
        background-color: #ccc;
      }
    }
  }

</style>



