<template>
  <div class="login_box">
    <Backbar title="修改密码"></Backbar>
    <div class="input_login">
      <input type="password" placeholder="旧密码" v-model="oldPassword" maxlength="18">
      <input type="password" placeholder="新密码" v-model="newPassword" maxlength="18">
      <input type="password" placeholder="确认密码" v-model="conPassword" maxlength="18">
      <button @click="changePassword">修改</button>
    </div>
  </div>
</template>

<script>
  import Backbar from './small_components/Back_bar';

  export default {
    name: 'ChangePassword',
    data () {
      return {
        oldPassword: '',
        newPassword: '',
        conPassword: ''
      };
    },
    mounted () {
      if (!this.$store.getters.getLogin) {
        this.$router.replace('/login');
      }
    },
    methods: {
      changePassword () {
        if (this.oldPassword === '') {
          alert('旧密码不能为空');
          return;
        }
        if (this.newPassword === '') {
          alert('新密码不能为空');
          return;
        }
        if (this.conPassword === '') {
          alert('确认密码不能为空');
          return;
        }
        if (this.conPassword !== this.newPassword) {
          alert('两次密码不一致');
          return;
        }
        this.$http.get(window.api.changePassword, {
          oldPassword: this.oldPassword,
          newPassword: this.newPassword
        }, {emulateJSON: true}).then(function (res) {
          var data = JSON.parse(res.bodyText);
          if (data.status === 'true') {
            alert(data.message);
            this.$store.dispatch('setLogin', false);
            this.$router.replace('/login');
          } else {
            alert(data.message);
          }
        });
      }
    },
    components: {
      'Backbar': Backbar
    }
  };
</script>

<style lang="less" scoped>
  .input_login {
    margin-top: 1.35rem;
    input {
      height: 1rem;
      width: 10rem;
      padding: .6rem;
      box-sizing: border-box;
      border: none;
      outline: none;
      font-size: .45rem;
      display: block;
    }
    button {
      margin: .6rem auto;
      display: block;
      width: 9rem;
      height: 1rem;
      font-size: .5rem;
      background: #4cd964;
      border: none;
      outline: none;
      color: #fff;
      border-radius: .1rem;
    }
  }
</style>



