<template>
  <div class="login_box">
    <Backbar title="登录"></Backbar>
    <div class="input_login">
      <input type="search" placeholder="用户名" v-model="username" maxlength="18" autocapitalize="off">
      <input type="password" placeholder="密码" v-model="password" maxlength="18" @keydown.enter="cheack_n_p">
      <button @click="cheack_n_p">登录</button>
      <p class="forget-password">
        <router-link to="/forgetpassword">忘记密码</router-link>
      </p>
    </div>
  </div>
</template>

<script>
import Backbar from './small_components/Back_bar';
import { mapGetters } from 'vuex';

export default {
  name: 'login',
  data () {
    return {
      username: '',
      password: ''
    };
  },
  mounted () {
    if (this.getLogin) {
      this.$router.replace('/myzone');
    }
  },
  computed: {
    ...mapGetters([
      'getLogin',
      'getUser_id',
      'getUsername',
      'getLoginCheck'
    ])
  },
  methods: {
    cheack_n_p () {
      if (this.uname === '' || this.pwd === '') {
        alert('用户名或密码不能为空');
        return;
      }
      this.$http.post(window.api.loginCheck, {
        name: this.username,
        password: this.password
      }, {emulateJSON: true}).then(function (res) {
        var data = JSON.parse(res.bodyText);
        if (data.status === 'true') {
          this.$store.dispatch('setLogin', true);
          this.$store.dispatch('setUser_id', data.user_id);
          this.$store.dispatch('setUsername', data.username);
          this.$router.replace('/myzone');
        } else {
          this.$store.dispatch('setLogin', false);
          alert(data.message);
        }
      });
    }
  },
  components: {
    'Backbar': Backbar
  }
};
</script>

<style lang="less" scoped>
.input_login{
  margin-top: 1.35rem;
  input{
    height: 1rem;
    width: 10rem;
    padding:.6rem;
    box-sizing:border-box;
    border:none;
    outline:none;
    font-size:.45rem;
    display:block;
  }
  button{
    margin: .6rem auto;
    display:block;
    width: 9rem;
    height: 1rem;
    font-size:.5rem;
    background:#4cd964;
    border:none;
    outline:none;
    color:#fff;
    border-radius:.1rem;
  }
  .forget-password{
    text-align: right;
    padding: 0 .5rem;
    a {
      font-size: .4rem;
      color: #0099FF;
    }
  }
}

</style>



