<template>
  <div>
    <Backbar title="订单详情"></Backbar>
    <div class="order_box">
      <div class="order_one">
        <div class="pannel-head">{{ item.shop_name }}</div>
        <div v-for=" (goods) in  item.goods_list" class="pannel-body">
          <div class="goods-img">
            <img v-bind:src="goods.goods_img" alt="">
          </div>
          <div class="goods-info">
            <header class="goods_info_t">
              <div class="goods_title flex_align">
                <p class="b_name">{{ goods.goods_name }}</p>
                <p class="b_amount">{{ goods.amount }}</p>
                <p class="b_price">￥{{ goods.goods_price }}</p>
              </div>
            </header>
          </div>
        </div>
      </div>
      <div class="order_one">
        <div class="pannel-head">配送信息</div>
        <div class="pannel-body">
          <div class="address-info">
            <header class="goods_info_t">
              <div class="goods_title flex_align">
                <p class="b_name">收货地址</p>
                <p class="b_address">{{ item.order_address }}</p>
              </div>
            </header>
          </div>
        </div>
      </div>
      <div class="order_one">
        <div class="pannel-head">订单信息</div>
        <div class="pannel-body">
          <div class="order-info">
            <header class="goods_info_t">
              <div class="goods_title flex_align">
                <p class="b_name">订单号</p>
                <p class="b_info">{{ item.order_code }}</p>
              </div>
              <div class="goods_title flex_align">
                  <p class="b_name">下单时间</p>
                  <p class="b_info">{{ item.order_birth_time }}</p>
              </div>
            </header>
          </div>
        </div>
      </div>

      <button v-if="item.order_status === '代付款'" @click="pay">立即支付 ￥{{ item.order_price }}</button>
    </div>
    <Fixednav></Fixednav>
  </div>
</template>

<script>
  import Backbar from './small_components/Back_bar';
  import Fixednav from './small_components/Fixed_nav';

  export default {
    name: 'orderDetail',
    data () {
      return {
        item: {}
      };
    },
    mounted () {
      if (!this.$store.getters.getLogin) {
        this.$router.replace('/login');
      }
      var parms = this.$route.query;
      var id = parms.id;
      this.$http.get(window.api.order + '?id=' + id).then(function (res) {
        var data = JSON.parse(res.bodyText);
        this.item = data;
      });
    },
    methods: {
      pay () {
        this.$http.post(window.api.pay, {

        }, {emulateJSON: true}).then(function (res) {
          var data = JSON.parse(res.bodyText);
          if (data.status === 'true') {
            alert(data.message);
            this.$router.replace('/order');
          } else {
            alert(data.message);
          }
        });
      }
    },
    components: {
      Backbar,
      Fixednav
    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
  .order_box {
    margin: 1rem 0 1.2rem;
  }
  button{
    margin: .6rem auto;
    display:block;
    width: 9rem;
    height: 1rem;
    font-size:.5rem;
    background:#4cd964;
    border:none;
    outline:none;
    color:#fff;
    border-radius:.1rem;
  }
  .order_one {
    background: #fff;
    margin-bottom: .2rem;
    .pannel-head{
      padding: .3rem .4rem;
      font-weight: bold;
      font-size: .4rem;
      border-bottom: 1px solid #666;
    }
    .pannel-body{
      padding: .3rem .4rem;
      .goods-img{
        width: 1rem;
        height: 1rem;
        float: left;
        img {
          width: 100%;
        }
      }
      .goods-info{
        height:1rem;
        line-height:1rem;
        margin-left: 1.4rem;
        font-size:.4rem;
        .b_name{
          width:8rem;
        }
        .b_amount{
          width:2rem;
        }
        .b_price{
          width:2rem;
          text-align:right;
        }
      }
      .address-info{
        font-size:.4rem;
        .b_name{
          width:3rem;
        }
        .b_address{
          width:8rem;
          text-align:left;
        }
      }
      .order-info{
        font-size:.4rem;
        .goods_title{
          padding: .3rem 0;
          .b_name{
            width:3rem;
          }
          .b_info{
            width:8rem;
            text-align:right;
          }
        }
      }
    }
  }
</style>



