<template>
  <div>
    <Backbar title="收货地址"></Backbar>
    <div class="address_box">
      <div v-for=" (item) in  addressList" class="address_one">
        <div class="pannel-body">
          <div class="address-info">
            <div class="flex_align">
              <div class="b_address">
                <p>
                  <span class="b_provence">{{ item.provence }}</span>
                  <span class="b_city">{{ item.city }}</span>
                  <span class="b_area">{{ item.area }}</span>
                </p>
                <p>
                  {{ item.detail_address }}
                </p>
                <p>
                  <span class="b_name">{{ item.name }}</span>
                  <span class="b_tel">{{ item.tel }}</span>
                </p>
              </div>
              <p class="b_operate">
                <router-link :to="'/editaddress?id='+item.id">修改</router-link>
              </p>
            </div>
          </div>
        </div>
      </div>
      <button @click="add_address">添加收货地址</button>
    </div>
    <Fixednav></Fixednav>
  </div>
</template>

<script>
  import Backbar from './small_components/Back_bar';
  import Fixednav from './small_components/Fixed_nav';

  export default {
    name: 'addressCon',
    data () {
      return {
        addressList: {}
      };
    },
    mounted () {
      if (!this.$store.getters.getLogin) {
        this.$router.replace('/login');
      }
      this.$http.get(window.api.addressList).then(function (res) {
        var data = JSON.parse(res.bodyText);
        this.addressList = data;
      });
    },
    methods: {
      add_address () {
        this.$router.replace('/addaddress');
      }
    },
    components: {
      Backbar,
      Fixednav
    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
  .address_box {
    margin: 1rem 0 1.2rem;
    button{
      margin: .6rem auto;
      display:block;
      width: 9rem;
      height: 1rem;
      font-size:.5rem;
      background:#4cd964;
      border:none;
      outline:none;
      color:#fff;
      border-radius:.1rem;
    }
  }

  .address_one {
    background: #fff;
    margin-bottom: .2rem;
    .pannel-head {
      padding: .3rem .4rem;
      font-weight: bold;
      font-size: .4rem;
      border-bottom: 1px solid #666;
    }
    .pannel-body {
      padding: .3rem .4rem;
      .address-info {
        font-size: .4rem;
        .b_address {
          width: 8.5rem;
          text-align: left;
        }
        .b_operate {
          width: 1.5rem;
          text-align: right;
          a {
            color: #0099FF;
          }
        }
      }
    }
  }
</style>



